import { getItem } from './robotDatabase'
/*
* socket, user, 
* chatdashboard
* @params: {func} openSocket socket
* @params: {func} initUser 
* @params: {func} currentChatDetails 
* @params: {func} getMsgList ajax
* @params: {object} user
* */
export default function (openSocket, initUser, currentChatDetails, getMsgList, user) {
  const { _id } = user

  if (!_id) {
    //  
    initUser().then(() => {
      // 
      getMsgList()
      openSocket()
    })
  } else {
    openSocket()
    if (currentChatDetails.length === getItem().length) getMsgList()
  }
}



