import React from 'react'
import { List, WingBlank, WhiteSpace, Radio, InputItem, Button } from 'antd-mobile'
import { Link } from 'react-router-dom'
import PropTypes from 'prop-types'

import wrapper from '../../login/view/wrapper'
import Logo from '../../../components/logo'
import Validator from '../../../utils/validator'
import { toastInfo } from '../../../utils/toast'
import User from '../../../service/user'

const { RadioItem } = Radio

class Register extends React.Component {
  constructor(props) {
    super(props)
    // event
    this.checkform = this.checkform.bind(this)
    this.doRegister = this.doRegister.bind(this)
    this.doLogin = this.doLogin.bind(this)
  }

  checkform() {
    const validator = new Validator()
    const {
      username,
      password,
      verifyPassword,
    } = this.props.state
    validator.add(username, 'noEmpty', '')
    validator.add(password, 'minLength:6', '6')
    validator.add([password, verifyPassword], 'sameValue', '')
    return validator.start()
  }

  doRegister() {
    const validateResult = this.checkform()
    if (validateResult) return toastInfo(validateResult)
    User.register(this.props.state).then((res) => {
      if (res && res.code === 2000) {
        this.doLogin()
      }
    })
  }

  doLogin() {
    this.props.doLogin(this.props.state).then((data) => {
      const { role, isFillInfo } = data // true 
      this.props.history.push(isFillInfo ? '/' : `${role}Setting`)
    })
  }

  render() {
    const {
      username,
      password,
      verifyPassword,
      role,
    } = this.props.state
    const { setFormState } = this.props
    return (
      <div className="register">
        <Logo/>
        <WingBlank>
          <List>
            <InputItem
              value={username}
              onChange={setFormState.bind(this, 'username')}
              placeholder=""
            >
              
            </InputItem>
            <InputItem
              value={password}
              onChange={setFormState.bind(this, 'password')}
              placeholder="6"
              type="password"
            >
              
            </InputItem>
            <InputItem
              value={verifyPassword}
              onChange={setFormState.bind(this, 'verifyPassword')}
              placeholder=""
              type="password"
            >
              
            </InputItem>
          </List>
          <WhiteSpace/>
          <List renderHeader="">
            <RadioItem
              value="boss"
              checked={role === 'boss'}
              onChange={setFormState.bind(this, 'role', 'boss')}
            >
              Boss
            </RadioItem>
            <RadioItem
              value="genius"
              checked={role === 'genius'}
              onChange={setFormState.bind(this, 'role', 'genius')}
            >
              
            </RadioItem>
          </List>
          <WhiteSpace size="xl"/>
          <Button onClick={this.doRegister} type="primary"></Button>
          <WhiteSpace/>
          <Link to="/login"></Link>
        </WingBlank>
      </div>
    )
  }
}

Register.propTypes = {
  doLogin: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
  state: PropTypes.object.isRequired,
  setFormState: PropTypes.func.isRequired
}

export default wrapper(Register)



