import React from 'react'
import { NavBar, List, WhiteSpace, InputItem, TextareaItem, Button } from 'antd-mobile'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'

import { toastInfo } from '../../../utils/toast'
import AvatarSelector from '../../../components/avatar-selector/'
import Validator from '../../../utils/validator'
import { actions } from '../../geniusSetting/'

const mapStateToProps = state => ({
  user: state.user
})

const mapDispatchToProps = (dispatch) => {
  return {
    fillInfo(data) {
      return dispatch(actions.fillInfo(data))
    }
  }
}

class BossSetting extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      avatar: '',
      nickname: '',
      company: '',
      job: '',
      salary: '',
      jobDescription: ''
    }
    this.onSelect = this.onSelect.bind(this)
    this.onInput = this.onInput.bind(this)
    this.submit = this.submit.bind(this)
  }

  onSelect({ icon }) {
    this.setState({
      avatar: icon
    })
  }

  onInput(type) {
    return (value) => {
      this.setState({
        [type]: value
      })
    }
  }

  submit() {
    const validator = new Validator()
    const errorMsg = {
      avatar: '',
      nickname: '',
      company: '',
      job: '',
      salary: '',
      jobDescription: ''
    }
    Object.keys(this.state).forEach((key) => {
      validator.add(this.state[key], 'noEmpty', errorMsg[key])
    })
    const validateResult = validator.start()
    if (validateResult) {
      toastInfo(validateResult)
    }
    this.props.fillInfo(this.state).then(() => {
      this.props.history.push('/')
    })
  }

  render() {
    const {
      nickname,
      company,
      job,
      salary,
      jobDescription
    } = this.state
    return (
      <div>
        <NavBar mode="dark">Boos</NavBar>
        <AvatarSelector onSelect={this.onSelect}/>
        <WhiteSpace/>
        <List>
          <InputItem
            value={nickname}
            onChange={this.onInput('nickname')}
            placeholder=""
          >
            
          </InputItem>
          <InputItem
            value={company}
            onChange={this.onInput('company')}
            placeholder=""
          >
            
          </InputItem>
          <InputItem
            value={job}
            onChange={this.onInput('job')}
            placeholder=""
          >
            
          </InputItem>
          <InputItem
            value={salary}
            onChange={this.onInput('salary')}
            placeholder="10k-15k"
          >
            
          </InputItem>
          <TextareaItem
            value={jobDescription}
            onChange={this.onInput('jobDescription')}
            rows={4}
            autoHeight
            placeholder=""
          />
        </List>
        <WhiteSpace/>
        <WhiteSpace/>
        <Button onClick={this.submit} type="primary"></Button>
      </div>
    )
  }
}

BossSetting.propTypes = {
  history: PropTypes.object.isRequired,
  fillInfo: PropTypes.func.isRequired
}

export default connect(mapStateToProps, mapDispatchToProps)(BossSetting)



