import React from 'react'
import { NavBar, List, WhiteSpace, InputItem, TextareaItem, Button } from 'antd-mobile'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'

import { toastInfo } from '../../../utils/toast'
import Validator from '../../../utils/validator'
import AvatarSelector from '../../../components/avatar-selector/'
import { fillInfo } from '../actions'

const mapDispatchToProps = (dispatch) => {
  return {
    fillInfo(data) {
      return dispatch(fillInfo(data))
    }
  }
}

class GeniusSetting extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      avatar: '',
      nickname: '',
      job: '',
      salary: '',
      selfDescription: ''
    }
    this.onSelect = this.onSelect.bind(this)
    this.onInput = this.onInput.bind(this)
    this.submit = this.submit.bind(this)
  }

  onSelect({ icon }) {
    this.setState({
      avatar: icon
    })
  }

  onInput(type) {
    return (value) => {
      this.setState({
        [type]: value
      })
    }
  }

  submit() {
    const validator = new Validator()
    const errorMsg = {
      avatar: '',
      nickname: '',
      job: '',
      salary: '',
      jobDescription: ''
    }
    Object.keys(this.state).forEach((key) => {
      validator.add(this.state[key], 'noEmpty', errorMsg[key])
    })
    const validateResult = validator.start()
    if (validateResult) {
      toastInfo(validateResult)
    }
    this.props.fillInfo(this.state).then(() => {
      this.props.history.push('/')
    })
  }

  render() {
    const {
      nickname,
      job,
      salary,
      selfDescription
    } = this.state
    return (
      <div>
        <NavBar mode="dark"></NavBar>
        <AvatarSelector onSelect={this.onSelect}/>
        <WhiteSpace/>
        <List>
          <InputItem
            placeholder=""
            value={nickname}
            onChange={this.onInput('nickname')}
          >
            
          </InputItem>
          <InputItem
            placeholder=""
            value={job}
            onChange={this.onInput('job')}
          >
            
          </InputItem>
          <InputItem
            placeholder="10k-15k"
            value={salary}
            onChange={this.onInput('salary')}
          >
            
          </InputItem>
          <TextareaItem
            value={selfDescription}
            onChange={this.onInput('selfDescription')}
            rows={4}
            autoHeight
            placeholder=""
          />
        </List>
        <WhiteSpace/>
        <WhiteSpace/>
        <Button onClick={this.submit} type="primary"></Button>
      </div>
    )
  }
}

GeniusSetting.propTypes = {
  history: PropTypes.object.isRequired,
  fillInfo: PropTypes.func.isRequired,
}

export default connect(null, mapDispatchToProps)(GeniusSetting)



